function sliderServices() {
    var swiper = new Swiper('.services .mySwiper', {
        slidesPerView: "auto",
        spaceBetween: 20,
        navigation: {
            nextEl: ".services .swiper-button-next",
            prevEl: ".services .swiper-button-prev",
        },
        breakpoints: {
            320: {
                spaceBetween: 10,
            },
            993: {
                spaceBetween: 20,
            },
        }
    });
}

$(document).ready(function () {
    sliderServices();

    // Menu
    $('.burger').click(function () {
        $('.menu').addClass('menu--active')
        $('body').addClass('fixed-body')
    })

    $('.menu__close').click(function () {
        $('.menu').removeClass('menu--active')
        $('body').removeClass('fixed-body')
    })

    // Header
    // let lastScrollTop = 0;
    // const header = $('#header');
    //
    // $(window).scroll(function() {
    //     let scrollTop = $(this).scrollTop();
    //
    //     if (scrollTop > lastScrollTop) {
    //         header.addClass('header-fixed');
    //     } else {
    //         header.removeClass('header-fixed');
    //     }
    //     lastScrollTop = scrollTop;
    // });

    // Btn-animation
    $(".btn-animation").mouseenter(function(e) {
        var $this = $(this);
        var $animation = $this.find(".circle");
        var offset = $this.offset();
        var x = e.pageX - offset.left;
        var y = e.pageY - offset.top;
        $animation.css({
            top: y + 'px',
            left: x + 'px'
        });
    });

    $(".btn-animation").mousemove(function(e) {
        var $this = $(this);
        var $animation = $this.find(".circle");
        var offset = $this.offset();
        var x = e.pageX - offset.left;
        var y = e.pageY - offset.top;
        $animation.css({
            top: y + 'px',
            left: x + 'px'
        });
    });

    // Form
    let isClicked = false;
    $('.form').on('submit', function(e) {
        e.preventDefault();

        isClicked = true;
        let allFilled = true;

        $(this).find('.form-group input').each(function() {
            const $input = $(this);
            const $formGroup = $input.closest('.form-group');

            if ($input.val().trim() === '') {
                $formGroup.addClass('form-group--active');
                allFilled = false;
            } else {
                $formGroup.removeClass('form-group--active');
            }
        });
    });
    $('.form-group input').on('input', function() {
        if (isClicked) {
            const $input = $(this);
            const $formGroup = $input.closest('.form-group');

            if ($input.val().trim() === '') {
                $formGroup.addClass('form-group--active');
            } else {
                $formGroup.removeClass('form-group--active');
            }
        }
    });

    // Up
    var t = $(".up");

    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            t.addClass("up--active");
        } else {
            t.removeClass("up--active");
        }
    });

    t.on("click", function(e) {
        e.preventDefault();
        $("html, body").animate({
            scrollTop: 0
        }, 300);
    });

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

    // Animation
    gsap.registerPlugin(ScrollTrigger);

    // Main
    function getAnimationParams() {
        const isMobile = window.matchMedia('(max-width: 1200px)').matches;
        if (isMobile) {
            return {
                mainItemsY: -250,
                mainItemsScale: 1.2,
            };
        } else {
            return {
                mainItemsY: -300,
                mainItemsScale: 1.6,
            };
        }
    }
    function createTimeline() {
        if (window.matchMedia('(max-width: 992px)').matches) {
            if (window.myTimeline) {
                window.myTimeline.kill();
                window.myTimeline = null;
            }
            return;
        }

        const params = getAnimationParams();

        if (window.myTimeline) {
            window.myTimeline.kill();
        }

        window.myTimeline = gsap.timeline({
            scrollTrigger: {
                trigger: ".main",
                start: "top top",
                end: "+=200%",
                scrub: true,
                pin: true,
            }
        })
            .to(".main__title", {
                opacity: 0,
                ease: "power1.out",
            }, 0)
            .to(".main__btn", {
                y: -100,
                opacity: 0,
                scale: 0.8,
                ease: "power1.inOut",
            }, 0)
            .to(".main__items", {
                y: params.mainItemsY,
                scale: params.mainItemsScale,
                ease: "power1.inOut",
            }, 0)
            .to({}, { duration: 0.5 });
    }
    createTimeline();
    window.addEventListener('resize', () => {
        createTimeline();
    });
    if (!window.matchMedia('(max-width: 992px)').matches) {
        var $main = $('.main');
        var originalBg = $main.data('background');

        $main.css('background-image', 'url(' + originalBg + ')');

        $('.main__item').on('mouseenter', function() {
            var newBg = $(this).data('background');
            $main.css('background-image', 'url(' + newBg + ')');
        });

        $('.main__item').on('mouseleave', function() {
            $main.css('background-image', 'url(' + originalBg + ')');
        });
    }

    // Main-mob
    const groups = gsap.utils.toArray('.main__mob-group');
    const container = document.querySelector('.main__mob');
    ScrollTrigger.create({
        trigger: container,
        start: "top top",
        end: () => `+=${window.innerHeight * (groups.length)}`,
        pin: true,
        scrub: 1,
        snap: 1 / (groups.length - 1),
        onUpdate: self => {
            const index = Math.round(self.progress * (groups.length - 1));
            groups.forEach((group, i) => {
                if (i === index) {
                    if (!group.classList.contains('active')) {
                        group.classList.add('active');

                        const items = group.querySelectorAll('.main__item');
                        items.forEach(item => {
                            gsap.fromTo(item,
                                { y: 50, opacity: 0 },
                                { y: 0, opacity: 1, duration: 0.8, ease: "power2.out" }
                            );
                        });
                    }
                } else {
                    group.classList.remove('active');
                }
            });
        }
    });

    // Gallery
    const section = document.querySelector('.gallery');
    const bigImage = document.querySelector('.gallery__img--big img');
    const grid1 = document.querySelector('.images-grid--1');
    const grid2 = document.querySelector('.images-grid--2');
    const grid3 = document.querySelector('.images-grid--3');
    const grid4 = document.querySelector('.images-grid--4');

    const gridAnim1 = gsap.to(grid1, {
        x: -20,
        ease: 'power1.inOut',
        paused: true
    });

    const gridAnim2 = gsap.to(grid2, {
        x: 20,
        ease: 'power1.inOut',
        paused: true
    });

    const gridAnim3 = gsap.to(grid3, {
        x: -20,
        ease: 'power1.inOut',
        paused: true
    });

    const gridAnim4 = gsap.to(grid4, {
        x: 20,
        ease: 'power1.inOut',
        paused: true
    });

    ScrollTrigger.create({
        trigger: bigImage,
        start: 'bottom bottom',
        end: 'bottom top',
        scrub: 0.5,
        onUpdate: self => {
            const progress = self.progress;

            gsap.to([gridAnim1, gridAnim2, gridAnim3, gridAnim4], {
                progress: progress,
                duration: 0.5,
                ease: 'power1.inOut'
            });

            if (progress > 0 && !section.classList.contains('gallery--active')) {
                section.classList.add('gallery--active');
            } else if (progress === 0 && section.classList.contains('gallery--active')) {
                section.classList.remove('gallery--active');
            }
        }
    });



    // Area
    const area = document.querySelector('.area');
    const images = [
        'img/area-img.webp',
        'img/area-img2.webp',
        'img/area-img3.webp',
        'img/area-img4.webp',
        'img/area-img5.webp',
        'img/area-img6.webp',
        'img/area-img7.webp',
        'img/area-img8.webp',
        'img/area-img9.webp',
        'img/area-img10.webp',
        'img/area-img11.webp',
        'img/area-img12.webp',
        'img/area-img13.webp',
        'img/area-img14.webp',
        'img/area-img15.webp',
        'img/area-img16.webp',
        'img/area-img17.webp',
        'img/area-img18.webp',
        'img/area-img19.webp',
        'img/area-img20.webp',
        'img/area-img21.webp',
        'img/area-img22.webp',
    ];
    images.forEach((src, i) => {
        const div = document.createElement('div');
        div.style.backgroundImage = `url(${src})`;
        div.style.position = 'absolute';
        div.style.top = 0;
        div.style.left = 0;
        div.style.width = '100%';
        div.style.height = '100%';
        div.style.backgroundSize = 'cover';
        div.style.backgroundPosition = 'center center';
        div.style.opacity = i === 0 ? '1' : '0';
        div.style.pointerEvents = 'none';
        div.classList.add('bg-slide');
        area.appendChild(div);
    });
    const bgSlides = gsap.utils.toArray('.bg-slide');
    const slideCount = bgSlides.length;
    const scrollLength = slideCount * 70;
    ScrollTrigger.create({
        trigger: area,
        start: "top top",
        end: "+=" + scrollLength,
        scrub: true,
        pin: true,
        onUpdate: self => {
            const progress = self.progress;
            let index = Math.floor(progress * slideCount);
            if (index >= slideCount) index = slideCount - 1;

            bgSlides.forEach((slide, i) => {
                slide.style.opacity = i === index ? '1' : '0';
            });
        }
    });




})

// Advantages
const advantagesTimeline = gsap.timeline({ paused: true });
function animateAdvantagesItems() {
    const items = document.querySelectorAll('.advantages__item');

    advantagesTimeline.clear();

    Array.from(items).reverse().forEach((item) => {
        advantagesTimeline.to(item, {
            opacity: 1,
            y: 0,
            duration: 0.6,
            ease: 'power2.out'
        }, '+=0');
    });

    advantagesTimeline.play();
}
const observerAdvantages = new IntersectionObserver((entries, observer) => {
    entries.forEach(entry => {
        if (entry.isIntersecting) {
            animateAdvantagesItems();
            observer.disconnect();
        }
    });
}, {
    threshold: 0.5
});
const advantagesSection = document.querySelector('.advantages__items');
if (advantagesSection) {
    observerAdvantages.observe(advantagesSection);
}

// Map
const mapSection = document.querySelector('.map');
const img1 = mapSection.querySelector('.map-img');
const img2 = mapSection.querySelector('.map-img2 img');
const img1DisappearLength = 150;
const img2AppearLength = 300;
const pinExtra = 200;
let scrollTriggers = [];
function clearTriggers() {
    scrollTriggers.forEach(st => st.kill && st.kill());
    scrollTriggers = [];
}
function animateMap() {
    clearTriggers();

    const isWide = window.innerWidth >= 992;

    const img1Bottom = () => img1.getBoundingClientRect().bottom + window.pageYOffset;
    const mapTop = () => mapSection.getBoundingClientRect().top + window.pageYOffset;

    const startPoint = isWide ? () => img1Bottom() - window.innerHeight : () => mapTop();

    const endPointImg1 = () => startPoint() + img1DisappearLength;

    const startPointImg2 = () => endPointImg1();
    const endPointImg2 = () => startPointImg2() + img2AppearLength;

    const pinEnd = () => endPointImg2() + pinExtra;

    scrollTriggers.push(ScrollTrigger.create({
        trigger: mapSection,
        start: startPoint,
        end: endPointImg1,
        onUpdate: self => {
            if (self.progress === 1) {
                mapSection.classList.add('map--active');
            } else {
                mapSection.classList.remove('map--active');
            }
        }
    }));

    scrollTriggers.push(gsap.to(img1, {
        opacity: 0,
        scale: 0.8,
        pointerEvents: 'none',
        ease: 'none',
        scrollTrigger: {
            trigger: mapSection,
            start: startPoint,
            end: endPointImg1,
            scrub: true,
        }
    }));

    scrollTriggers.push(gsap.to(img2, {
        display: 'block',
        opacity: 1,
        top: 0,
        right: 0,
        width: '100%',
        pointerEvents: 'auto',
        ease: 'none',
        scrollTrigger: {
            trigger: mapSection,
            start: startPointImg2,
            end: endPointImg2,
            scrub: true,
        }
    }));

    scrollTriggers.push(ScrollTrigger.create({
        trigger: mapSection,
        start: startPoint,
        end: pinEnd,
        pin: true,
        pinSpacing: true,
        anticipatePin: 1,
    }));
}
const observerMap = new IntersectionObserver((entries, observer) => {
    entries.forEach(entry => {
        if (entry.isIntersecting) {
            animateMap();
            observer.disconnect();
        }
    });
}, { threshold: 0.5 });
if (mapSection) {
    observerMap.observe(mapSection);
}
window.addEventListener('resize', () => {
    if (ScrollTrigger.getAll().length) {
        animateMap();
    }
});

// House
const house = document.querySelector('.house');
const observerHouse = new IntersectionObserver((entries, observer) => {
    entries.forEach(entry => {
        if (entry.isIntersecting) {
            house.classList.add('house--active');
        } else {
            house.classList.remove('house--active');
        }
    });
}, { threshold: 0.5 });
const houseSection = document.querySelector('.house__img');
if (houseSection) {
    observerHouse.observe(houseSection);
}

// Services
const serviceTimeline = gsap.timeline({ paused: true });
const observerServices = new IntersectionObserver((entries, observer) => {
    entries.forEach(entry => {
        if (entry.isIntersecting) {
            serviceTimeline.clear();

            document.querySelectorAll('.services__slider .swiper-slide').forEach((item, index) => {
                serviceTimeline.to(item, {
                    opacity: 1,
                    y: 0,
                    duration: 0.6,
                    ease: 'power2.out'
                });
            });

            serviceTimeline.play();
            observer.disconnect();
        }
    });
}, { threshold: 0.5 });
const servicesSection = document.querySelector('.services__slider');
if (servicesSection) {
    observerServices.observe(servicesSection);
}




